// Reescribir colores
$negro: #000 ; 
$blanco: #fff ; 
$morado: #a435f0 ;
$gris: #f8f9fb;
$grisOscuro: #6a6f73  ;
$naranja: #b4690e; 

//Tipografia
$fuenteHeading: 'kaisei harunoUmi', serif ; 
$fuenteTextos: Arial, Helvetica, sans-serif ; 
$headings-font-family: $fuenteHeading ; 
$headings-font-weight: 700 ; 
$h2-font-size: 3rem ; 
$h3-font-size: 2.6rem ; 

//Colores
$secondary:  $naranja;
$success: $morado;
$light: $gris ;
$dark: $negro; 

//formulariois
$input-border-radius: 1rem ;
$input-border-color: $dark;

// Enlaces
$nav-link-color: $grisOscuro ;
$nav-link-hover-color: $dark  ;
$nav-link-font-weight: 700  ;
$nav-link-font-size: .9rem  ;

//Botones
$btn-border-radius:  0;
$btn-font-weight:  700;


//cards
$card-spacer-x: 0 ;
$card-border-radius: 0 ;
$card-border-width: 0 ;

//Habilitar css grid
$enable-cssgrid: true;



// Importar Bootstrap
@import '../../node_modules/bootstrap/scss/bootstrap.scss';

